<template>
  <div class="app">
    <a-form :form="form" @submit="handleSubmit">
      <a-form-item class="form-item-title" label="新闻标题">
        <a-input
          v-decorator="[
          'news_title',
          {rules: [{ required: true, message: '请输入新闻标题!' }], initialValue:news_title,}
        ]"
          placeholder="请输入新闻标题"
        />
      </a-form-item>
      <!-- <a-form-item class="form-item-title" label="新闻栏目" :wrapper-col="{ span: 12 }">
        <a-select
          v-decorator="[
          'news_type',
          {rules: [{ required: true, message: '请选择新闻栏目!' }],initialValue:news_type}
        ]"
          placeholder="选择栏目"
        >
          <a-select-option value="male">花花新闻</a-select-option>
          <a-select-option value="female">周边新闻</a-select-option>
        </a-select>
      </a-form-item>-->
      <a-form-item class="form-item-title" label="新闻栏目" v-if="residences.length>0">
        <a-cascader
          @change="handleChange"
          v-decorator="[
          'news_type',
          {
            rules: [{ type: 'array', required: true, message: '请选择新闻栏目!' }],
          }
        ]"
          placeholder="选择栏目"
          :options="residences"
        />
      </a-form-item>
      <a-form-item>
        <mceeditor v-model="news_content"></mceeditor>
        <!-- Value:{{Value}} -->
      </a-form-item>
      <a-form-item :wrapper-col="{ span: 12, offset: 5 }">
        <a-button type="primary" html-type="submit">提交</a-button>
      </a-form-item>
    </a-form>
    <!--<mce-editor 
    :config           = "Config"
     v-model          = "Value"
    :url              = "Url" //图片上传地址
    :max-size         = "MaxSize"//文件上传最大值
    :accept           = "Accept"//接收什么格式的图片
    :with-credentials = false
    @on-ready         = "onEditorReady"//后面什么的没研究了
    @on-destroy       = "onEditorDestroy"
    @on-upload-success= "onEditorUploadComplete"
    @on-upload-fail   = "onEditorUploadFail"
    ></mce-editor>-->
  </div>
</template>

<script>
import { insertnews, getnewitem } from "@/api/news";
import mceeditor from "@/components/Tinymce";
window.tinymce.baseURL = "/static/tinymce";
window.tinymce.suffix = ".min";

export default {
  components: {
    mceeditor
  },
  data() {
    return {
      news_content: "",
      news_title: "",
      news_type: "",
      formLayout: "horizontal",
      form: this.$form.createForm(this),
      residences: []
    };
  },
  created() {
    this.init();
  },
  methods: {
    init() {
      let { params } = this.$route.query;
      if (params) {
        params = JSON.parse(params);
        console.log("xw", params);
        this.news_content = params.news_content;
        this.news_title = params.news_title;
        this.news_type = params.news_type;
      }
      getnewitem().then(res => {
        let { data } = res;
        if (data.code == 200) {
          for (let item of data.data) {
            this.residences.push({
              value: item.columnlevel,
              label: item.columnlevel
            });
          }
        }
      });
    },
    handleChange(value) {
      let _index = 0;
      this.residences.forEach((item, index) => {
        if (item.value == value) {
          _index = index;
        }
      });
    },
    handleSubmit(e) {
      e.preventDefault();
      this.form.validateFields((err, values) => {
        if (!err) {
          insertnews({
            news_title: values.news_title,
            news_type: values.news_type,
            news_content: this.news_content
          }).then(res => {
            this.$message.success("新增成功");
          });
        }
      });
    }
  }
};
</script>

<style lang="less" scoped>
.app {
  width: 1204px;
  margin: 0 auto;
  overflow: hidden;
  .form-item-title {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
  }
}
</style>


